<template>
  <!--<div id="app">-->
  <div>
    <!--
    <img src="./assets/logo.png">-->
    <!-- If you want to show survey, uncomment the line below -->
    <!-- <survey :survey="survey"></survey> -->
   <survey :survey="survey"></survey>
    <!-- If you want to show survey editor, uncomment the line below -->
    <!-- <survey-editor></survey-editor> -->
  </div>
</template>

<script>
// import SurveyEditor from './components/SurveyEditor'
import * as SurveyVue from 'survey-vue'

// import 'bootstrap/dist/css/bootstrap.css';
import * as widgets from 'surveyjs-widgets'

var Survey = SurveyVue.Survey
Survey.cssType = 'bootstrap'

// import "inputmask/dist/inputmask/phone-codes/phone.js";

// widgets.icheck(SurveyVue);
// widgets.select2(SurveyVue);
// widgets.imagepicker(SurveyVue);
// widgets.inputmask(SurveyVue);
// widgets.jquerybarrating(SurveyVue);
// widgets.jqueryuidatepicker(SurveyVue);
// widgets.nouislider(SurveyVue);
// widgets.select2tagbox(SurveyVue);
widgets.signaturepad(SurveyVue)
// widgets.sortablejs(SurveyVue);
// widgets.ckeditor(SurveyVue);
// widgets.autocomplete(SurveyVue);
// widgets.bootstrapslider(SurveyVue);

export default {
  name: 'MySurvey',
  components: {
    Survey
  },
  data () {
    var json = {
      locale: 'en',
      title: 'Infection Control Assessment Contact',
      pages: [
        {
          name: 'page0',
          elements: [
            {
              type: 'html',
              name: 'question1',
              html: {
                en: 'Under section 2.30(c) of the Quality Agency Principles 2013 the Quality Agency may use an assessment contact for the purpose of assessing  the approved provider’s performance against the Accreditation Standards or Flexible Care Standards. Information collected will be managed under the provisions of the Quality Agency Act 2013 and the Quality Agency Principles 2013. A summary report on infection control management practices based on data collected through all the assessment contacts conducted across Australia will be provided to the Secretary of the Department of Health.'
              }
            },
            {
              type: 'panel',
              name: 'panel1',
              elements: [
                {
                  type: 'text',
                  name: 'Service Name',
                  isRequired: true
                },
                {
                  type: 'text',
                  name: 'RACS ID',
                  isRequired: true,
                  validators: [
                    {
                      type: 'numeric',
                      text: '0000',
                      minValue: 0,
                      maxValue: 9999
                    }
                  ],
                  inputType: 'number'
                },
                {
                  type: 'text',
                  name: 'Approved Provider',
                  isRequired: true
                },
                {
                  type: 'text',
                  name: 'No. of Allocated Places',
                  isRequired: true,
                  inputType: 'number'
                },
                {
                  type: 'text',
                  name: 'No. of Staff',
                  isRequired: true,
                  inputType: 'number'
                },
                {
                  type: 'text',
                  name: 'No. of Volunteers',
                  isRequired: true,
                  inputType: 'number'
                },
                {
                  type: 'text',
                  name: 'Postcode',
                  description: {
                    en: 'rovide the postcode associated with the location of the Service.'
                  },
                  isRequired: true,
                  validators: [
                    {
                      type: 'numeric',
                      minValue: 0,
                      maxValue: 9999
                    }
                  ],
                  inputType: 'number'
                }
              ],
              title: {
                en: 'Service Details'
              }
            }
          ]
        },
        {
          name: 'page1',
          elements: [
            {
              type: 'panel',
              elements: [
                {
                  type: 'text',
                  name: 'full name',
                  isRequired: true
                },
                {
                  type: 'text',
                  name: 'position at service',
                  description: {
                    en: 'The position that is held at the Service'
                  },
                  isRequired: true
                },
                {
                  type: 'signaturepad',
                  name: 'sign',
                  title: 'Please enter your signature',
                  width: '600'
                },
                {
                  type: 'checkbox',
                  name: 'Approve Submission',
                  title: {
                    en: 'Approve Submission'
                  },
                  description: {
                    en: 'By ticking this box I am declaring that I am authorised to provide the information in this form as a delegate of the above Approved Provider for the Service named. I also declare that the information is correct at the date of submission.'
                  },
                  isRequired: true,
                  choices: [
                    {
                      value: 'approved',
                      text: {
                        en: 'I approve this submission'
                      }
                    }
                  ]
                }
              ],
              title: {
                en: 'Authority'
              }
            }
          ]
        },
        {
          name: 'page2',
          elements: [
            {
              type: 'panel',
              elements: [
                {
                  type: 'radiogroup',
                  name: 'question1',
                  title: {
                    en: 'Question 1'
                  },
                  description: {
                    en: 'Does the Service have an infection control plan to prevent, identify and contain influenza and other respiratory viruses?'
                  },
                  valueName: 'infection_control_plan_flu',
                  isRequired: true,
                  choices: [
                    'yes',
                    'no'
                  ],
                  colCount: 2
                },
                {
                  type: 'radiogroup',
                  name: 'question1a',
                  visible: false,
                  visibleIf: "{infection_control_plan_flu} == 'yes'",
                  title: {
                    en: 'Question 1a'
                  },
                  description: {
                    en: 'If yes, was it reviewed in the last 12 months?'
                  },
                  isRequired: true,
                  choices: [
                    'yes',
                    'no'
                  ],
                  colCount: 2
                },
                {
                  type: 'radiogroup',
                  name: 'question2',
                  title: {
                    en: 'Question 2'
                  },
                  description: {
                    en: 'Does the Service have an infection control plan to prevent, identify and contain gastroenteritis?'
                  },
                  valueName: 'infection_control_plan_gastro',
                  isRequired: true,
                  choices: [
                    'yes',
                    'no'
                  ],
                  colCount: 2
                },
                {
                  type: 'radiogroup',
                  name: 'question2a',
                  visible: false,
                  visibleIf: "{infection_control_plan_gastro} == 'yes'",
                  title: {
                    en: 'Question 2a'
                  },
                  description: {
                    en: 'If yes, was it reviewed in the last 12 months?'
                  },
                  isRequired: true,
                  choices: [
                    'yes',
                    'no'
                  ],
                  colCount: 2
                }
              ],
              title: {
                en: 'Governance'
              }
            }
          ]
        },
        {
          name: 'page3',
          elements: [
            {
              type: 'panel',
              elements: [
                {
                  type: 'radiogroup',
                  name: 'question3',
                  title: {
                    en: 'Question 3'
                  },
                  description: {
                    en: 'Does the Service have an annual influenza vaccination program for the workforce?'
                  },
                  valueName: 'vax_prog_workforce',
                  isRequired: true,
                  choices: [
                    'yes',
                    'no'
                  ],
                  colCount: 2
                },
                {
                  type: 'radiogroup',
                  name: 'question3a',
                  visibleIf: "{vax_prog_workforce} == 'yes'",
                  title: {
                    en: 'Question 3a'
                  },
                  description: {
                    en: 'If yes in Question 3, is participation in the program mandatory?'
                  },
                  isRequired: true,
                  choices: [
                    'yes',
                    'no'
                  ],
                  colCount: 2
                },
                {
                  type: 'checkbox',
                  name: 'question3b',
                  visible: false,
                  visibleIf: "{vax_prog_workforce} == 'yes'",
                  title: {
                    en: 'Question 3b'
                  },
                  description: {
                    en: 'If yes in Question 3, does the Service:'
                  },
                  isRequired: true,
                  hasOther: true,
                  choices: [
                    'provide access to influenza vaccination for staff',
                    'encourage staff to go to an external provider (eg. GP)'
                  ],
                  otherText: {
                    en: 'other - what does the Service do?'
                  }
                },
                {
                  type: 'checkbox',
                  name: 'question4',
                  title: {
                    en: 'Question 4'
                  },
                  description: {
                    en: 'Does the Service have a policy about vaccination for staff in the following categories? (check all boxes that apply)'
                  },
                  isRequired: true,
                  choices: [
                    'All Staff',
                    'Nurse practitioners',
                    'Registered nurses',
                    'Enrolled nurses',
                    'Personal care attendants',
                    'Allied health professionals',
                    'Allied health assistants',
                    'Management',
                    'Administration',
                    'Spiritual/pastoral care',
                    'Ancillary care',
                    'Maintenance and cleaning',
                    'Catering and food preparation',
                    'Volunteers',
                    'None of the above'
                  ],
                  colCount: 3
                },
                {
                  type: 'radiogroup',
                  name: 'question5',
                  description: {
                    en: 'If yes in Question 5, is participation in the program mandatory?'
                  },
                  valueName: 'vax_prog_care_recip',
                  isRequired: true,
                  choices: [
                    'yes',
                    'no'
                  ]
                },
                {
                  type: 'radiogroup',
                  name: 'question5a',
                  visible: false,
                  visibleIf: "{vax_prog_care_recip} == 'yes'",
                  title: {
                    en: 'Question 5a'
                  },
                  description: {
                    en: 'If yes in Question 5, is participation in the program mandatory?'
                  },
                  isRequired: true,
                  choices: [
                    'yes',
                    'no'
                  ],
                  colCount: 2
                },
                {
                  type: 'radiogroup',
                  name: 'question5b',
                  visible: false,
                  visibleIf: "{vax_prog_care_recip} == 'yes'",
                  title: {
                    en: 'Question 5b'
                  },
                  description: {
                    en: 'If yes in Question 5, does the Service:'
                  },
                  isRequired: true,
                  choices: [
                    'provide access to influenza vaccination for care recipients ',
                    'encourage care recipients to go to an external provider (eg. GP) ',
                    'none of the above '
                  ],
                  colCount: 2
                },
                {
                  type: 'checkbox',
                  name: 'question6',
                  title: {
                    en: 'Question 6'
                  },
                  description: {
                    en: 'Does the Service maintain a current register of influenza vaccination records for the following? (check all boxes that apply)'
                  },
                  valueName: 'vax_reg',
                  isRequired: true,
                  choices: [
                    'Staff',
                    'Agency staff',
                    'Volunteers',
                    'Care recipients',
                    {
                      value: 'no_reg',
                      text: 'No register is kept'
                    }
                  ],
                  colCount: 3
                },
                {
                  type: 'checkbox',
                  name: 'question6a',
                  visible: false,
                  visibleIf: "{vax_reg} notempty &&\n{vax_reg} <> 'no_reg'",
                  title: {
                    en: 'Question 6a'
                  },
                  description: {
                    en: 'If a register is kept, do the following provide evidence of their vaccination e.g. documentation or receipt? (check all boxes that apply)'
                  },
                  choices: [
                    'Staff',
                    'Agency staff',
                    'Volunteers',
                    'Care recipients',
                    'No evidence required'
                  ],
                  colCount: 3
                },
                {
                  type: 'text',
                  name: 'question7',
                  title: {
                    en: 'Question 7'
                  },
                  description: {
                    en: 'In the past 12 months what percentage of Staff had an influenza vaccination? (enter a number between 0-100 or unknown if the percentage is not known)'
                  },
                  validators: [
                    {
                      type: 'numeric',
                      minValue: 0,
                      maxValue: 100
                    }
                  ],
                  inputType: 'number'
                }
              ],
              title: {
                en: 'Immunisation'
              }
            }
          ]
        }
      ],
      showQuestionNumbers: 'off',
      showProgressBar: 'top',
      storeOthersAsComment: false,
      pagePrevText: 'Previous',
      pageNextText: 'Continue',
      completeText: 'Finish',
      requiredText: ''
    }
    var model = new SurveyVue.Model(json)
    return {
      survey: model
    }
  }
}
</script>



